<template>
  <div id="contain">
    <div class="blogcard"
         v-for="item in list"
         :key="item.postId">
      <p class="blogtitle"
         @click="goToPost(item.postuuid)">{{item.posttitle}}</p>
      <p class="blogcontent">{{ item.postcontent.substring(0,200).replace(/<[^>]+>/g,"") }}...</p>
      <div class="blogbottom">
        <div class="blogbottom-left">
          <button class="read-more-btn"
                  @click="goToPost(item.postuuid)">Read More</button>
        </div>
      </div>
    </div>
    <div class="pagination">
      <div class="pagination-left">
        <button v-if="pageId!==1"
                class="pagination-btn"
                @click="goToPage('before')">上一页</button>
      </div>
      <div class="pagination-right">
        <button v-if="pageId!==maxpage"
                class="pagination-btn"
                @click="goToPage('next')">下一页</button>
      </div>
    </div>
  </div>
</template>
<script>
import { getBlogList } from '@/api'
export default {
  data () {
    return {
      pageId: null,
      maxpage: null,
      list: []
    }
  },
  methods: {
    getBlogList () {
      getBlogList(this.pageId).then(res => {
        this.list = res.results.rows
        this.maxpage = Math.ceil(parseInt(res.results.count) / 5)
      })
    },
    goToPost (id) {
      this.$router.push('/blog/post/' + id)
    },
    goToPage (type) {
      if (type === 'next') {
        this.pageId = parseInt(this.pageId) + 1
        this.$router.push('/blog/page/' + this.pageId)
        this.getBlogList()
      }
      if (type === 'before') {
        this.pageId = parseInt(this.pageId) - 1
        this.$router.push('/blog/page/' + this.pageId)
        this.getBlogList()
      }
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    }
  },
  created () {
    if (!this.$route.params.id) {
      this.pageId = 1
    } else {
      this.pageId = this.$route.params.id
    }
    this.getBlogList()
  }
}
</script>
