<template>
  <div id="contain">
    <div class="postcard">
      <p class="posttitle">{{post.posttitle}}</p>
      <p class="posttime">{{time}}</p>
      <article class="markdown-body"
               v-html="post.postcontent">
        {{post.postcontent}}
      </article>
      <p class="postcopy"> 作者: 毓彦 </p>

        <p class="postcopy" >链接: <a :href="link">{{link}}</a></p>

        <p class="postcopy">许可协议: <a href="https://creativecommons.org/licenses/by-nc-sa/4.0/">知识共享署名-非商业性使用 4.0 国际许可协议</a></p>
    </div>
    <br/>
  </div>
</template>
<script>
import { getBlogById } from '@/api'
export default {
  data () {
    return {
      postId: null,
      post: {},
      time:'',
      link:''
    }
  },
  methods: {
    getBlog () {
      getBlogById(this.postId).then(res => {
        this.post = res.results
        this.time = this.post.updatedAt.substr(0,4)+'-'+this.post.updatedAt.substr(5,2) +'-'+this.post.updatedAt.substr(8,2) 
      })
    }
  },
  created () {
    this.postId = this.$route.params.id
    this.link = 'https://lolita.im/#' + this.$route.path
    this.getBlog()
  }
}
</script>
